"use client"

import { useRouter, useSearchParams } from "next/navigation"
import { useAuth } from "@/app/auth-context"
import { AdminDashboard } from "@/components/admin-dashboard"
import { UserDashboard } from "@/components/user-dashboard"
import { Button } from "@/components/ui/button"
import { DropdownMenu, DropdownMenuContent, DropdownMenuItem, DropdownMenuTrigger } from "@/components/ui/dropdown-menu"
import { Menu, Monitor, Users } from "lucide-react"
import { useEffect, useState } from "react"
import Swal from "sweetalert2"

export default function Dashboard() {
  const { user, logout, isLoading } = useAuth()
  const router = useRouter()
  const searchParams = useSearchParams()
  const [menuOpen, setMenuOpen] = useState(false)

  useEffect(() => {
    if (!isLoading && !user) {
      router.push("/")
    }
  }, [user, isLoading, router])

  // จัดการเมื่อคลิกปุ่มจาก LINE
  useEffect(() => {
    const ticketId = searchParams.get('ticket')
    const action = searchParams.get('action')

    if (!ticketId || !action || user?.role !== 'admin') return

    // จัดการปุ่ม "รับงาน"
    if (action === 'accept') {
      Swal.fire({
        title: 'รับงาน',
        text: `คุณต้องการรับงาน ${ticketId} หรือไม่?`,
        icon: 'question',
        showCancelButton: true,
        confirmButtonText: 'รับงาน',
        cancelButtonText: 'ยกเลิก',
        confirmButtonColor: '#06C755'
      }).then(async (result) => {
        if (result.isConfirmed) {
          try {
            // อัปเดตสถานะเป็น "กำลังดำเนินการ" (status = 1)
            const response = await fetch(`/api/tickets/${ticketId}`, {
              method: 'PUT',
              headers: { 'Content-Type': 'application/json' },
              body: JSON.stringify({ status: '1' })
            })

            if (response.ok) {
              await Swal.fire({
                icon: 'success',
                title: 'สำเร็จ!',
                text: 'รับงานเรียบร้อยแล้ว',
                timer: 2000,
                showConfirmButton: false
              })
              // รีหน้าให้ข้อมูลล่าสุดและล้าง query params
              router.replace('/dashboard')
              window.location.reload()
            } else {
              throw new Error('Failed to update status')
            }
          } catch (error) {
            Swal.fire({
              icon: 'error',
              title: 'เกิดข้อผิดพลาด',
              text: 'ไม่สามารถรับงานได้',
              confirmButtonText: 'ตกลง'
            })
          }
        } else {
          router.push('/dashboard')
        }
      })
    }
    
    // จัดการปุ่ม "เสร็จสิ้น"
    if (action === 'complete') {
      Swal.fire({
        title: 'เสร็จสิ้น',
        text: `คุณต้องการเปลี่ยนสถานะงาน ${ticketId} เป็น "รอการประเมิน" หรือไม่?`,
        icon: 'question',
        showCancelButton: true,
        confirmButtonText: 'เสร็จสิ้น',
        cancelButtonText: 'ยกเลิก',
        confirmButtonColor: '#06C755'
      }).then(async (result) => {
        if (result.isConfirmed) {
          try {
            // อัปเดตสถานะเป็น "รอการประเมิน" (status = 4)
            const response = await fetch(`/api/tickets/${ticketId}`, {
              method: 'PUT',
              headers: { 'Content-Type': 'application/json' },
              body: JSON.stringify({ status: '4' })
            })

            if (response.ok) {
              await Swal.fire({
                icon: 'success',
                title: 'สำเร็จ!',
                text: 'เปลี่ยนสถานะเป็น "รอการประเมิน" แล้ว',
                timer: 2000,
                showConfirmButton: false
              })
              router.push('/dashboard')
            } else {
              throw new Error('Failed to update status')
            }
          } catch (error) {
            Swal.fire({
              icon: 'error',
              title: 'เกิดข้อผิดพลาด',
              text: 'ไม่สามารถเปลี่ยนสถานะได้',
              confirmButtonText: 'ตกลง'
            })
          }
        } else {
          router.push('/dashboard')
        }
      })
    }
  }, [searchParams, user, router])

  if (isLoading) {
    return <div className="flex items-center justify-center min-h-screen">Loading...</div>
  }

  if (!user) {
    return null
  }

  const handleLogout = () => {
    logout()
    router.push("/")
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-white border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4 flex items-center justify-between">
          <div className="flex items-center gap-4">
            <DropdownMenu open={menuOpen} onOpenChange={setMenuOpen}>
              <DropdownMenuTrigger asChild>
                <Button 
                  variant="outline" 
                  size="icon"
                  className="h-10 w-10 rounded-lg border-2 hover:bg-gray-100 hover:border-gray-400 transition-all"
                >
                  <Menu className="h-5 w-5" />
                </Button>
              </DropdownMenuTrigger>
              <DropdownMenuContent 
                align="start" 
                className="w-56 mt-2 shadow-lg border-2"
                sideOffset={5}
              >
                {user.role === "admin" ? (
                  <>
                    <div className="px-2 py-1.5 text-xs font-semibold text-gray-500 uppercase tracking-wider">
                      เมนูผู้ดูแลระบบ
                    </div>
                    <DropdownMenuItem 
                      onClick={() => router.push('/assets')}
                      className="cursor-pointer py-3 px-3 hover:bg-blue-50 focus:bg-blue-50"
                    >
                      <Monitor className="h-5 w-5 mr-3 text-blue-600" />
                      <span className="text-base">จัดการทรัพย์สิน</span>
                    </DropdownMenuItem>
                    <DropdownMenuItem 
                      onClick={() => router.push('/users')}
                      className="cursor-pointer py-3 px-3 hover:bg-green-50 focus:bg-green-50"
                    >
                      <Users className="h-5 w-5 mr-3 text-green-600" />
                      <span className="text-base">จัดการผู้ใช้</span>
                    </DropdownMenuItem>
                    {/* <DropdownMenuItem 
                      onClick={() => router.push('/sites')}
                      className="cursor-pointer py-3 px-3 hover:bg-purple-50 focus:bg-purple-50"
                    >
                      <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 mr-3 text-purple-600" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                        <path d="M3 9l9-7 9 7v11a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2z"></path>
                        <polyline points="9 22 9 12 15 12 15 22"></polyline>
                      </svg>
                      <span className="text-base">จัดการสาขา</span>
                    </DropdownMenuItem> */}
                  </>
                ) : (
                  <>
                    <div className="px-2 py-1.5 text-xs font-semibold text-gray-500 uppercase tracking-wider">
                      เมนูผู้ใช้งาน
                    </div>
                    <DropdownMenuItem 
                      onClick={() => router.push('/my-assets')}
                      className="cursor-pointer py-3 px-3 hover:bg-blue-50 focus:bg-blue-50"
                    >
                      <Monitor className="h-5 w-5 mr-3 text-blue-600" />
                      <span className="text-base">ทรัพย์สินของคุณ</span>
                    </DropdownMenuItem>
                  </>
                )}
              </DropdownMenuContent>
            </DropdownMenu>
            <div>
              <h1 className="text-2xl font-bold">ระบบบำรุงรักษา</h1>
              <p className="text-sm text-muted-foreground">
                {user.role === "admin" ? "Admin Dashboard" : "User Dashboard"}
              </p>
            </div>
          </div>
          <div className="flex items-center gap-4">
            <div className="text-right">
              <p className="font-medium text-sm">{user.name}</p>
              <p className="text-xs text-muted-foreground capitalize">{user.role}</p>
            </div>
            <Button variant="outline" onClick={handleLogout}>
              Logout
            </Button>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {user.role === "admin" ? <AdminDashboard /> : <UserDashboard />}
      </main>
    </div>
  )
}
