"use client"

import { useEffect, useState } from "react"
import type { Ticket } from "@/lib/types"
import { TicketCard } from "./ticket-card"
import { TicketDetail } from "./ticket-detail"
import { UserTicketForm } from "./user-ticket-form"
import { EditTicketDialog } from "./edit-ticket-dialog"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Monitor, Plus } from "lucide-react"
import { useAuth } from "@/app/auth-context"
import Swal from "sweetalert2"

export function AdminDashboard() {
  const { user } = useAuth()
  const [tickets, setTickets] = useState<Ticket[]>([])
  const [selectedTicket, setSelectedTicket] = useState<Ticket | null>(null)
  const [filterStatus, setFilterStatus] = useState("all")
  const [searchTerm, setSearchTerm] = useState("")
  const [isLoading, setIsLoading] = useState(true)
  const [isUpdating, setIsUpdating] = useState(false)
  const [activeTab, setActiveTab] = useState<"active" | "completed">("active")
  const [showCreateForm, setShowCreateForm] = useState(false)
  const [isCreatingTicket, setIsCreatingTicket] = useState(false)
  const [editingTicket, setEditingTicket] = useState<any>(null)
  const [isEditDialogOpen, setIsEditDialogOpen] = useState(false)

  useEffect(() => {
    fetchTickets()
  }, [])

  const fetchTickets = async () => {
    try {
      const response = await fetch("/api/tickets")
      const data = await response.json()
      setTickets(data)
    } catch (error) {
      console.error("Failed to fetch tickets:", error)
    } finally {
      setIsLoading(false)
    }
  }

  const handleCreateTicket = async (data: any) => {
    setIsCreatingTicket(true)
    try {
      const response = await fetch("/api/tickets", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(data),
      })

      if (response.ok) {
        await Swal.fire({
          icon: 'success',
          title: 'สำเร็จ!',
          text: 'สร้างคำขอเรียบร้อยแล้ว',
          confirmButtonText: 'ตกลง',
          timer: 2000
        })
        await fetchTickets()
        setShowCreateForm(false)
      } else {
        await Swal.fire({
          icon: 'error',
          title: 'เกิดข้อผิดพลาด',
          text: 'ไม่สามารถสร้างคำขอได้',
          confirmButtonText: 'ตกลง'
        })
      }
    } catch (error) {
      console.error("Failed to create ticket:", error)
      await Swal.fire({
        icon: 'error',
        title: 'เกิดข้อผิดพลาด',
        text: 'ไม่สามารถเชื่อมต่อกับเซิร์ฟเวอร์ได้',
        confirmButtonText: 'ตกลง'
      })
    } finally {
      setIsCreatingTicket(false)
    }
  }

  const handleStatusChange = async (status: string) => {
    if (!selectedTicket) return

    // Get current and new status text
    const statusText = {
      0: "รอดำเนินการ",
      1: "กำลังดำเนินการ",
      2: "เสร็จสิ้น",
      3: "ยกเลิก",
      4: "รอการประเมิน",
    }
    
    const Swal = (await import('sweetalert2')).default
    
    const result = await Swal.fire({
      title: 'ยืนยันการเปลี่ยนสถานะ?',
      html: `เปลี่ยนสถานะเป็น: <strong>${statusText[status]}</strong>`,
      icon: 'question',
      showCancelButton: true,
      confirmButtonText: 'ยืนยัน',
      cancelButtonText: 'ยกเลิก',
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33'
    })
    
    if (!result.isConfirmed) return

    setIsUpdating(true)
    try {
      // Auto-set start_repair if status is 1 (in progress)
      // Auto-set finish_repair if status is 2 (completed) or 3 (cancelled)
      const updateData: any = { status }
      
      if (status === '1' && !selectedTicket.start_repair) {
        updateData.start_repair = new Date().toLocaleString('sv-SE', { timeZone: 'Asia/Bangkok' }).replace(' ', ' ')
      }
      
      if ((status === '2' || status === '3') && !selectedTicket.finish_repair) {
        updateData.finish_repair = new Date().toLocaleString('sv-SE', { timeZone: 'Asia/Bangkok' }).replace(' ', ' ')
      }

      // Auto-calculate total_date when setting finish_repair
      if ((status === '2' || status === '3')) {
        const startRepair = selectedTicket.start_repair || selectedTicket[' start_repair']
        if (startRepair && startRepair.trim() !== '') {
          const start = new Date(startRepair)
          const finish = updateData.finish_repair ? new Date(updateData.finish_repair) : new Date()
          const diffTime = Math.abs(finish.getTime() - start.getTime())
          const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24))
          updateData.total_date = `${diffDays} วัน`
        }
      }

      const response = await fetch(`/api/tickets/${selectedTicket.request_id}`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(updateData),
      })

      if (response.ok) {
        const updatedTicket = await response.json()
        setSelectedTicket(updatedTicket)
        setTickets(tickets.map((t) => (t.request_id === updatedTicket.request_id ? updatedTicket : t)))
        await fetchTickets() // Refresh the list
        
        await Swal.fire({
          icon: 'success',
          title: 'สำเร็จ!',
          text: 'อัปเดตสถานะเรียบร้อยแล้ว',
          timer: 2000,
          showConfirmButton: false
        })
      } else {
        const errorData = await response.json().catch(() => ({}))
        await Swal.fire({
          icon: 'error',
          title: 'เกิดข้อผิดพลาด',
          text: errorData.error || 'ไม่สามารถอัปเดตสถานะได้',
          confirmButtonText: 'ตกลง'
        })
      }
    } catch (error) {
      console.error("Failed to update ticket:", error)
      await Swal.fire({
        icon: 'error',
        title: 'เกิดข้อผิดพลาด',
        text: 'ไม่สามารถเชื่อมต่อกับเซิร์ฟเวอร์ได้',
        confirmButtonText: 'ตกลง'
      })
    } finally {
      setIsUpdating(false)
    }
  }

  const filteredTickets = tickets.filter((ticket) => {
    const matchesSearch =
      (ticket.asset_id || "").toLowerCase().includes(searchTerm.toLowerCase()) ||
      (ticket.request_id || "").toLowerCase().includes(searchTerm.toLowerCase()) ||
      (ticket.username || "").toLowerCase().includes(searchTerm.toLowerCase())
    const matchesStatus = filterStatus === "all" || ticket.Status === Number(filterStatus)
    const matchesTab = activeTab === "active" 
      ? (ticket.Status !== 2 && ticket.Status !== 3)
      : (ticket.Status === 2 || ticket.Status === 3)
    return matchesSearch && matchesStatus && matchesTab
  })

  const stats = {
    total: tickets.length,
    pending: tickets.filter((t) => t.Status === 0).length,
    inProgress: tickets.filter((t) => t.Status === 1).length,
    completed: tickets.filter((t) => t.Status === 2).length,
    cancelled: tickets.filter((t) => t.Status === 3).length,
    assessment: tickets.filter((t) => t.Status === 4).length,
  }

  return (
    <div className="space-y-6">
      {/* Quick Actions */}
      <div className="flex gap-2">
        <Button 
          variant="default" 
          className="gap-2"
          onClick={() => setShowCreateForm(true)}
        >
          <Plus className="h-4 w-4" />
          สร้างคำขอใหม่
        </Button>
      </div>

      {/* Stats */}
      <div className="grid grid-cols-1 md:grid-cols-6 gap-4">
        <div className="bg-white p-4 rounded-lg border">
          <p className="text-sm text-muted-foreground">จำนวนคำขอพึงหมด</p>
          <p className="text-2xl font-bold">{stats.total}</p>
        </div>
        <div className="bg-yellow-50 p-4 rounded-lg border border-yellow-200">
          <p className="text-sm text-muted-foreground">รอดำเนินการ</p>
          <p className="text-2xl font-bold text-yellow-700">{stats.pending}</p>
        </div>
        <div className="bg-blue-50 p-4 rounded-lg border border-blue-200">
          <p className="text-sm text-muted-foreground">กำลังดำเนินการ</p>
          <p className="text-2xl font-bold text-blue-700">{stats.inProgress}</p>
        </div>
        <div className="bg-orange-50 p-4 rounded-lg border border-orange-200">
          <p className="text-sm text-muted-foreground">รอการประเมิน</p>
          <p className="text-2xl font-bold text-orange-700">{stats.assessment}</p>
        </div>
        <div className="bg-green-50 p-4 rounded-lg border border-green-200">
          <p className="text-sm text-muted-foreground">เสร็จสิ้น</p>
          <p className="text-2xl font-bold text-green-700">{stats.completed}</p>
        </div>
        <div className="bg-red-50 p-4 rounded-lg border border-red-200">
          <p className="text-sm text-muted-foreground">ยกเลิก</p>
          <p className="text-2xl font-bold text-red-700">{stats.cancelled}</p>
        </div>

      </div>

      {/* Tab Buttons */}
      <div className="flex items-center justify-between border-b">
        <div className="flex gap-2">
          <button
            onClick={() => setActiveTab("active")}
            className={`px-6 py-3 font-medium transition-colors border-b-2 ${
              activeTab === "active"
                ? "border-primary text-primary"
                : "border-transparent text-muted-foreground hover:text-foreground"
            }`}
          >
            กำลังดำเนินการ ({tickets.filter(t => t.Status !== 2 && t.Status !== 3).length})
          </button>
          <button
            onClick={() => setActiveTab("completed")}
            className={`px-6 py-3 font-medium transition-colors border-b-2 ${
              activeTab === "completed"
                ? "border-primary text-primary"
                : "border-transparent text-muted-foreground hover:text-foreground"
            }`}
          >
            เสร็จสิ้น/ยกเลิก ({tickets.filter(t => t.Status === 2 || t.Status === 3).length})
          </button>
        </div>
      </div>

      {/* Filters */}
      <div className="flex flex-col md:flex-row gap-2">
        <Input
          placeholder="ค้นหาด้วย Request ID, Asset ID, หรือชื่อผู้แจ้ง..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="flex-1"
        />
        <Select value={filterStatus} onValueChange={setFilterStatus}>
          <SelectTrigger className="w-full md:w-40">
            <SelectValue placeholder="Filter by status" />
          </SelectTrigger>
          <SelectContent>
            <SelectItem value="all">สถานะพึงหมด</SelectItem>
            {activeTab === "active" ? (
              <>
                <SelectItem value="0">รอดำเนินการ</SelectItem>
                <SelectItem value="1">กำลังดำเนินการ</SelectItem>
                <SelectItem value="4">รอการประเมิน</SelectItem>
              </>
            ) : (
              <>
                <SelectItem value="2">เสร็จสิ้น</SelectItem>
                <SelectItem value="3">ยกเลิก</SelectItem>
              </>
            )}
          </SelectContent>
        </Select>
      </div>

      {/* Main Content */}
      <div className="space-y-3">
        {isLoading ? (
          <p className="text-center text-muted-foreground py-8">Loading tickets...</p>
        ) : filteredTickets.length === 0 ? (
          <p className="text-center text-muted-foreground py-8">No tickets found</p>
        ) : (
          filteredTickets.map((ticket) => (
            <div key={ticket.request_id} className="relative">
              {ticket.Status === 0 && (
                <div className="absolute bottom-4 right-4 z-10 flex gap-2">
                  <Button
                    size="sm"
                    className="bg-blue-600 hover:bg-blue-700 text-white shadow-md"
                    onClick={async (e) => {
                      e.stopPropagation()
                      const Swal = (await import('sweetalert2')).default
                      const result = await Swal.fire({
                        title: 'รับเรื่อง?',
                        text: 'ต้องการเปลี่ยนสถานะเป็น "กำลังดำเนินการ" หรือไม่?',
                        icon: 'question',
                        showCancelButton: true,
                        confirmButtonText: 'รับเรื่อง',
                        cancelButtonText: 'ยกเลิก',
                        confirmButtonColor: '#2563eb',
                        cancelButtonColor: '#6b7280'
                      })
                      
                      if (result.isConfirmed) {
                        setIsUpdating(true)
                        try {
                          const updateData: any = { 
                            status: '1',
                            start_repair: new Date().toLocaleString('sv-SE', { timeZone: 'Asia/Bangkok' }).replace(' ', ' ')
                          }
                          
                          const response = await fetch(`/api/tickets/${ticket.request_id}`, {
                            method: "PUT",
                            headers: { "Content-Type": "application/json" },
                            body: JSON.stringify(updateData),
                          })

                          if (response.ok) {
                            await fetchTickets()
                            await Swal.fire({
                              icon: 'success',
                              title: 'รับเรื่องสำเร็จ!',
                              text: 'เปลี่ยนสถานะเป็น "กำลังดำเนินการ" แล้ว',
                              timer: 2000,
                              showConfirmButton: false
                            })
                          } else {
                            throw new Error('Failed to update')
                          }
                        } catch (error) {
                          await Swal.fire({
                            icon: 'error',
                            title: 'เกิดข้อผิดพลาด',
                            text: 'ไม่สามารถรับเรื่องได้',
                            confirmButtonText: 'ตกลง'
                          })
                        } finally {
                          setIsUpdating(false)
                        }
                      }
                    }}
                  >
                    รับเรื่อง
                  </Button>
                  <Button
                    size="sm"
                    variant="outline"
                    className="bg-white hover:bg-gray-50 shadow-md"
                    onClick={(e) => {
                      e.stopPropagation()
                      setSelectedTicket(ticket)
                    }}
                  >
                    ดูรายละเอียด
                  </Button>
                  <Button
                    size="sm"
                    variant="destructive"
                    className="shadow-md"
                    onClick={async (e) => {
                      e.stopPropagation()
                      const Swal = (await import('sweetalert2')).default
                      const result = await Swal.fire({
                        title: 'ยกเลิกคำขอ?',
                        text: 'ต้องการยกเลิกคำขอนี้หรือไม่?',
                        icon: 'warning',
                        showCancelButton: true,
                        confirmButtonText: 'ยกเลิกคำขอ',
                        cancelButtonText: 'ไม่ยกเลิก',
                        confirmButtonColor: '#dc2626',
                        cancelButtonColor: '#6b7280'
                      })
                      
                      if (result.isConfirmed) {
                        setIsUpdating(true)
                        try {
                          const response = await fetch(`/api/tickets/${ticket.request_id}`, {
                            method: "PUT",
                            headers: { "Content-Type": "application/json" },
                            body: JSON.stringify({ Status: 3, cancel_whit: user?.username }),
                          })

                          if (response.ok) {
                            await fetchTickets()
                            await Swal.fire({
                              icon: 'success',
                              title: 'ยกเลิกสำเร็จ!',
                              text: 'เปลี่ยนสถานะเป็น "ยกเลิก" แล้ว',
                              timer: 2000,
                              showConfirmButton: false
                            })
                          } else {
                            throw new Error('Failed to update')
                          }
                        } catch (error) {
                          await Swal.fire({
                            icon: 'error',
                            title: 'เกิดข้อผิดพลาด',
                            text: 'ไม่สามารถยกเลิกได้',
                            confirmButtonText: 'ตกลง'
                          })
                        } finally {
                          setIsUpdating(false)
                        }
                      }
                    }}
                  >
                    ยกเลิก
                  </Button>
                </div>
              )}
              {ticket.Status === 1 && (
                <div className="absolute bottom-4 right-4 z-10 flex gap-2">
                  <Button
                    size="sm"
                    variant="outline"
                    className="bg-white hover:bg-gray-50 shadow-md"
                    onClick={(e) => {
                      e.stopPropagation()
                      setEditingTicket(ticket)
                      setIsEditDialogOpen(true)
                    }}
                  >
                    รายละเอียดการซ่อม
                  </Button>
                  <Button
                    size="sm"
                    variant="default"
                    className="shadow-md"
                    onClick={(e) => {
                      e.stopPropagation()
                      setSelectedTicket(ticket)
                    }}
                  >
                    ดูรายละเอียด
                  </Button>
                  <Button
                    size="sm"
                    variant="destructive"
                    className="shadow-md"
                    onClick={async (e) => {
                      e.stopPropagation()
                      const Swal = (await import('sweetalert2')).default
                      const result = await Swal.fire({
                        title: 'ยกเลิกคำขอ?',
                        text: 'ต้องการยกเลิกคำขอนี้หรือไม่?',
                        icon: 'warning',
                        showCancelButton: true,
                        confirmButtonText: 'ยกเลิกคำขอ',
                        cancelButtonText: 'ไม่ยกเลิก',
                        confirmButtonColor: '#dc2626',
                        cancelButtonColor: '#6b7280'
                      })
                      
                      if (result.isConfirmed) {
                        setIsUpdating(true)
                        try {
                          const response = await fetch(`/api/tickets/${ticket.request_id}`, {
                            method: "PUT",
                            headers: { "Content-Type": "application/json" },
                            body: JSON.stringify({ Status: 3, cancel_whit: user?.username }),
                          })

                          if (response.ok) {
                            await fetchTickets()
                            await Swal.fire({
                              icon: 'success',
                              title: 'ยกเลิกสำเร็จ!',
                              text: 'เปลี่ยนสถานะเป็น "ยกเลิก" แล้ว',
                              timer: 2000,
                              showConfirmButton: false
                            })
                          } else {
                            throw new Error('Failed to update')
                          }
                        } catch (error) {
                          await Swal.fire({
                            icon: 'error',
                            title: 'เกิดข้อผิดพลาด',
                            text: 'ไม่สามารถยกเลิกได้',
                            confirmButtonText: 'ตกลง'
                          })
                        } finally {
                          setIsUpdating(false)
                        }
                      }
                    }}
                  >
                    ยกเลิก
                  </Button>
                </div>
              )}
              {(ticket.Status === 2 || ticket.Status === 3 || ticket.Status === 4) && (
                <div className="absolute bottom-4 right-4 z-10">
                  <Button
                    size="sm"
                    variant="default"
                    className="shadow-md"
                    onClick={(e) => {
                      e.stopPropagation()
                      setSelectedTicket(ticket)
                    }}
                  >
                    ดูรายละเอียด
                  </Button>
                </div>
              )}
              <TicketCard ticket={ticket} />
            </div>
          ))
        )}
      </div>

      {/* Detail Modal */}
      {selectedTicket && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-lg max-w-4xl w-full max-h-[90vh] overflow-y-auto">
            <TicketDetail
              ticket={selectedTicket}
              isAdmin={true}
              onStatusChange={handleStatusChange}
              onClose={() => setSelectedTicket(null)}
              isUpdating={isUpdating}
              onUpdate={fetchTickets}
            />
          </div>
        </div>
      )}

      {/* Create Ticket Form Modal */}
      {showCreateForm && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-lg max-w-2xl w-full max-h-[90vh] overflow-y-auto">
            <div className="p-6">
              <div className="flex items-center justify-between mb-4">
                <h2 className="text-xl font-semibold">สร้างคำขอใหม่</h2>
                <Button
                  variant="ghost"
                  size="sm"
                  onClick={() => setShowCreateForm(false)}
                  disabled={isCreatingTicket}
                >
                  ✕
                </Button>
              </div>
              <UserTicketForm 
                onSubmit={handleCreateTicket} 
                isLoading={isCreatingTicket} 
                userName={user?.name || ""} 
              />
            </div>
          </div>
        </div>
      )}

      {/* Edit Ticket Dialog */}
      {editingTicket && (
        <EditTicketDialog
          ticket={editingTicket}
          isOpen={isEditDialogOpen}
          onClose={() => {
            setIsEditDialogOpen(false)
            setEditingTicket(null)
          }}
          onSuccess={fetchTickets}
        />
      )}
    </div>
  )
}
