"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter } from "@/components/ui/dialog"
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group"
import { Label } from "@/components/ui/label"
import Swal from "sweetalert2"

interface EditTicketDialogProps {
  ticket: any
  isOpen: boolean
  onClose: () => void
  onSuccess: () => void
}

export function EditTicketDialog({ ticket, isOpen, onClose, onSuccess }: EditTicketDialogProps) {
  const [formData, setFormData] = useState({
    Status: ticket.Status ?? 0,
    start_repair: ticket.start_repair || "",
    finish_repair: ticket.finish_repair ? new Date(ticket.finish_repair).toISOString().split('T')[0] : "",
    cost: ticket.cost || "",
    total_date: ticket.total_date || "",
    Rep_info: ticket.Rep_info || "",
    type_of_work: ticket.type_of_work || "",
    work: ticket.work || "",
    detail_work: ticket.detail_work || "",
  })
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [hasCost, setHasCost] = useState<string>(ticket.cost && ticket.cost !== "0" ? "yes" : "")

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsSubmitting(true)

    try {
      const submitData = {
        Rep_info: formData.Rep_info,
        cost: formData.cost.trim() || "0",
        type_of_work: formData.type_of_work,
        work: formData.work,
        detail_work: formData.detail_work,
        status: '4', // เปลี่ยนสถานะเป็นรอการประเมิน
      }

      const response = await fetch(`/api/tickets/${ticket.request_id}`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(submitData),
      })

      if (response.ok) {
        await Swal.fire({
          icon: 'success',
          title: 'สำเร็จ!',
          text: 'บันทึกข้อมูลเรียบร้อยแล้ว สถานะเปลี่ยนเป็น "รอการประเมิน"',
          timer: 2000,
          showConfirmButton: false
        })
        onSuccess()
        onClose()
      } else {
        const errorData = await response.json().catch(() => ({}))
        console.error('API Error:', errorData)
        await Swal.fire({
          icon: 'error',
          title: 'เกิดข้อผิดพลาด',
          text: errorData.error || 'ไม่สามารถบันทึกข้อมูลได้',
          confirmButtonText: 'ตกลง'
        })
      }
    } catch (error) {
      console.error('Submit Error:', error)
      await Swal.fire({
        icon: 'error',
        title: 'เกิดข้อผิดพลาด',
        text: 'ไม่สามารถเชื่อมต่อกับเซิร์ฟเวอร์ได้',
        confirmButtonText: 'ตกลง'
      })
    } finally {
      setIsSubmitting(false)
    }
  }

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-2xl max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle>รายละเอียดการซ่อม - {ticket.request_id}</DialogTitle>
        </DialogHeader>
        
        <form onSubmit={handleSubmit} className="space-y-4">
          {/* ชนิดของงาน */}
          <div className="space-y-2">
            <label className="text-sm font-medium">ชนิดของงาน</label>
            <Input
              placeholder="เช่น Hardware, Software, Network"
              value={formData.work}
              disabled
              className="bg-muted cursor-not-allowed"
            />
          </div>
          {/* ประเภทของงาน */}
          <div className="space-y-2">
            <label className="text-sm font-medium">ประเภทของงาน</label>
            <Input
              placeholder="เช่น ซ่อม, ติดตั้ง, บำรุงรักษา"
              value={formData.type_of_work}
              disabled
              className="bg-muted cursor-not-allowed"
            />
          </div>

          {/* รายละเอียดของงาน */}
          <div className="space-y-2">
            <label className="text-sm font-medium">รายละเอียดของงาน</label>
            <textarea
              placeholder="รายละเอียดเพิ่มเติมเกี่ยวกับประเภทงาน..."
              value={formData.detail_work}
              disabled
              rows={3}
              className="w-full rounded-md border border-input bg-muted px-3 py-2 text-sm cursor-not-allowed"
            />
          </div>

          {/* อาการ/ปัญหาที่แจ้ง */}
          <div className="space-y-2">
            <label className="text-sm font-medium">อาการ/ปัญหาที่แจ้ง</label>
            <textarea
              placeholder="ไม่มีข้อมูล"
              value={ticket.problem_description || ""}
              disabled
              rows={3}
              className="w-full rounded-md border border-input bg-muted px-3 py-2 text-sm cursor-not-allowed"
            />
          </div>

          {/* รายละเอียดการซ่อม */}
          <div className="space-y-2">
            <label className="text-sm font-medium">รายละเอียดการซ่อม <span className="text-red-500">*</span></label>
            <textarea
              placeholder="ระบุรายละเอียดวิธีการซ่อม อะไหล่ที่เปลี่ยน..."
              value={formData.Rep_info}
              onChange={(e) => setFormData({ ...formData, Rep_info: e.target.value })}
              required
              rows={6}
              className="w-full rounded-md border border-input bg-background px-3 py-2 text-sm"
            />
          </div>

          {/* ค่าใช้จ่าย */}
          <div className="space-y-3">
            <label className="text-sm font-medium">มีค่าใช้จ่ายหรือไม่</label>
            <RadioGroup
              value={hasCost}
              onValueChange={(value) => {
                setHasCost(value)
                if (value === "no") {
                  setFormData({ ...formData, cost: "0" })
                }
              }}
              className="flex gap-4"
            >
              <div className="flex items-center space-x-2">
                <RadioGroupItem value="yes" id="cost-yes" />
                <Label htmlFor="cost-yes" className="cursor-pointer">มีค่าใช้จ่าย</Label>
              </div>
              <div className="flex items-center space-x-2">
                <RadioGroupItem value="no" id="cost-no" />
                <Label htmlFor="cost-no" className="cursor-pointer">ไม่มีค่าใช้จ่าย</Label>
              </div>
            </RadioGroup>
          </div>

          {/* ช่องกรอกค่าใช้จ่าย - แสดงเฉพาะเมื่อเลือก "มีค่าใช้จ่าย" */}
          {hasCost === "yes" && (
            <div className="space-y-2">
              <label className="text-sm font-medium">ค่าใช้จ่าย</label>
              <Input
                type="text"
                placeholder="ระบุค่าใช้จ่าย (ถ้าไม่กรอกจะเป็น 0)"
                value={formData.cost}
                onChange={(e) => setFormData({ ...formData, cost: e.target.value })}
              />
            </div>
          )}

          <DialogFooter>
            <Button type="button" variant="outline" onClick={onClose} disabled={isSubmitting}>
              ยกเลิก
            </Button>
            <Button type="submit" disabled={isSubmitting}>
              {isSubmitting ? "กำลังบันทึก..." : "บันทึก"}
            </Button>
          </DialogFooter>
        </form>
      </DialogContent>
    </Dialog>
  )
}
